.hero {
  width: 100%;
  background: $red-to-blue-to-red;
  padding-bottom: 51px;
  padding-top: 51px;

  @include vp-1199 {
    padding-top: 82px;
  }

  @include vp-767 {
    padding-bottom: 80px;
  }
}

.hero__container {
  width: 85%;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;

  @include vp-1199 {
    width: 88%;
    justify-content: space-between;
  }

  @include vp-767 {
    width: 91%;
    justify-content: start;
  }
}

.hero__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 180px 200px 150px;
  width: 43%;
  padding-right: 64px;

  @include vp-1199 {
    width: 46%;
    grid-template-rows: 100px 180px 90px;
    padding-right: 0;
  }

  @include vp-767 {
    width: 100%;
    position: relative;
  }
}

.hero__fitness {
  grid-column-start: 2;
  grid-row-start: 2;
}

.hero__fitness p {
  font-family: "AlumniSans", "Verdana", sans-serif;
  font-style: normal;
  font-size: 92px;
  line-height: 110px;
  color: $color-default-white;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-align: right;
  margin: 0;

  @include vp-1199 {
    font-size: 72px;
    line-height: 86px;
    text-transform: none;
  }
}

.hero__town {
  grid-column-start: 2;
  grid-row-start: 1;
}

.hero__town p {
  font-family: "AlumniSans", "Verdana", sans-serif;
  font-style: normal;
  font-size: 48px;
  line-height: 58px;
  color: $color-default-white;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  text-align: right;
  margin: 0;
  padding-top: 120px;

  @include vp-1199 {
    font-size: 34px;
    line-height: 41px;
    padding-top: 60px;
  }

  @include vp-767 {
    font-size: 28px;
    line-height: 33px;
  }
}

.hero__wrapper ul {
  text-align: right;
  grid-column-start: 1;
  grid-row-start: 2;
  padding-right: 20px;
  padding-top: 120px;

  @include vp-1199 {
    padding-top: 85px;
    padding-right: 8px;
  }

  @include vp-767 {
    position: absolute;
    padding: 0;
    top: 75px;
    right: calc(175px);
  }
}

.hero__wrapper li {
  font-family: "RobotoCondensed", "Verdana", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 21px;
  color: $color-default-white;
  margin: 0;
}

.hero__wrapper-link {
  grid-column-start: 2;
  grid-row-start: 3;

  @include vp-767 {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 3;
  }
}

.hero__link {
  display: block;
  width: 100%;
  font-family: "RobotoCondensed", "Verdana", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 12px;
  line-height: 14px;
  color: $color-default-white;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 4px solid $color-default-white;
  text-align: center;
  padding: 24px 57px;
  margin-top: 80px;

  @include vp-1199 {
    margin-top: 5px;
    padding: 24px 33px;
  }

  @include vp-767 {
    margin-top: 20px;
  }
}

.hero__link:hover,
.hero__link:focus {
  background-color: var(--color-bg-base);
  color: var(--color-text-additional);
  outline: none;
}

.hero__link:active {
  background-color: $color-default-white;
  color: $color-default-sapphire;
}

.hero__link:disabled {
  opacity: 0.5;
}

.hero__media {
  margin-top: -5px;
  background-color: $color-default-persian-blue;

  @include vp-1199 {
    margin-top: 0;
  }

  @include vp-767 {
    display: none;
  }
}

.hero__media img {
  object-fit: cover;

  @include vp-1199 {
    width: 324px;
    height: 390px;
  }
}
