.swiper-coaches {
  width: 91.2%;
  margin: 0 auto;

  @include vp-1199 {
    width: 83.48%;
  }

  @include vp-767 {
    width: 77.93%;
  }
}

.swiper-coaches h2 {
  font-family: "AlumniSans", "Verdana", sans-serif;
  font-style: normal;
  font-size: 54px;
  line-height: 64.8px;
  color: $color-default-sapphire;
  letter-spacing: 0.03em;
  padding-bottom: 48px;
  margin: 0;

  @include vp-1199 {
    text-align: center;
    padding-bottom: 31px;
  }

  @include vp-767 {
    padding-bottom: 50px;
  }
}

.swiper-coaches__wrapper {
  margin: 0 auto;
}

.swiper-coaches__slide {
  position: relative;
  outline: none;
}

.swiper-coaches__media {
  background-color: $color-default-dark-grey;
}

.swiper-coaches__media img {
  object-fit: cover;
  width: 100%;

  @include vp-1199 {
    margin: 0 auto;
    width: 268px;
    height: 294px;
  }

  @include vp-767 {
    width: 226px;
    height: 274px;
  }
}

.swiper-slide:hover .coaches-info,
.swiper-slide:focus .coaches-info {
  display: block;
}

.swiper-coaches__slide p {
  position: absolute;
  top: 87%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: "RobotoCondensed", "Verdana", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  line-height: 27px;
  color: $color-default-white;
  margin: 0;
}

.swiper-slide:hover p,
.swiper-slide:focus p {
  display: none;
}
