.free-lesson {
  width: 50%;
  padding: 56px 0 70px 0;
  background-color: $color-default-sapphire;

  label {
    input {
      border: none;
      padding: 10px 0 10px 0;
      min-height: auto;
      border-bottom: 1px solid $color-default-slate-grey;
      max-width: 100vw;
    }
  }

  @include vp-1199 {
    width: 100%;
    padding: 70px 0 70px 0;
  }
}

.free-lesson__container {
  width: 60.03%;
  margin-left: 15.08%;

  @include vp-1199 {
    width: 61.46%;
    margin: 0 auto;
  }

  @include vp-767 {
    width: 90.62%;
    margin: 0 auto;
  }
}

.free-lesson__container h2 {
  font-family: "AlumniSans", "Verdana", sans-serif;
  font-style: normal;
  font-size: 54px;
  line-height: 84.24px;
  color: $color-default-white;
  letter-spacing: 0.03em;
  margin: 0;
  padding-bottom: 14px;

  @include vp-1199 {
    font-size: 42px;
    line-height: 50.4px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 38.4px;
  }
}

.free-lesson__personal {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border: none;
  margin: 0;
  padding: 0 0 30px 0;
}

.free-lesson__wrapper {
  width: 46.34%;
  padding: 0;

  @include vp-767 {
    width: 100%;
  }
}

.free-lesson__name,
.free-lesson__tel {
  font-family: "RobotoCondensed", "Verdana", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 16.41px;
  color: $color-default-white;
  background-color: $color-default-sapphire;
  outline: none;
  width: 100%;
}

.free-lesson__name::placeholder,
.free-lesson__tel::placeholder {
  color: $color-default-white;
}

.free-lesson__send {
  width: 100%;
  font-family: "RobotoCondensed", "Verdana", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 14px;
  line-height: 16.41px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  background-color: $color-default-sapphire;
  border: 3px solid $color-default-white;
  padding: 21px 0 21px 0;
  cursor: pointer;
}
